import React, {Component} from "react";
import PropTypes from 'prop-types';
import {connect} from "react-redux";
import BuyMoreProductsComponent from "./BuyMoreProductsComponent";
import VasModal from "./VasModal";
import {getMobileVases} from "eshop/modules/cart/selectors";
import {fetchMobileVases} from "eshop/modules/cart/actions/cart";
import VasEntryComponent from "eshop/modules/cart/components/entriesList/mobile/productEntries/VasEntryComponent"
import {getFrontValidationMsg} from "eshop/modules/checkout/selectors"
import MulticartValidationComponent from "eshop/modules/checkout/components/MulticartValidationComponent";
import TransactionProcessType from "../../../../../core/constants/TransactionProcessTypeEnum";

class SelectRetentionBonusComponent extends Component {
    constructor(props) {
        super(props);
        this.state = {
            modalVisible: false
        }
        this.addButtonClicked = this.addButtonClicked.bind(this);
        this.closeModalClicked = this.closeModalClicked.bind(this);
    }

    componentWillMount() {
        if (this.props.entry.bundleCode && this.props.entry.productCode && this.props.entry.processType) {
            this.props.fetchMobileVases(this.props.entry.bundleCode, this.props.entry.productCode, this.props.entry.processType);
        }
    }

    addButtonClicked() {
        console.log("Modal clicked");
        this.setState({modalVisible: true}, function () {
            console.log("This state modalVisible: " + !!this.state.modalVisible);
        }.bind(this));
    }

    closeModalClicked() {
        this.setState({modalVisible: false}, function () {
            console.log("This state modalVisible: " + !!this.state.modalVisible);
        }.bind(this));
    }


    containsVases() {
        let vases = (this.props.vases || []).filter(v => v.propositionId == this.props.entry.bundleCode);
        if(vases.length > 0 && vases.categorizedBonuses){
            console.log("RetentionBonuses containsVases2",vases);

        }
        return vases.length > 0 && vases[0].categorizedBonuses &&  vases[0].categorizedBonuses["RetentionBonuses"] && vases[0].categorizedBonuses["RetentionBonuses"].services && vases[0].categorizedBonuses["RetentionBonuses"].services.length > 0;
    }

    getVasesForBundle(bundle) {
        return this.props.vases && this.props.vases.find(vas => vas.bundle == bundle);
    }

    getRetentionBonuses(){
        let vases = this.props.vases.find(mv => mv.process ==TransactionProcessType.RETENTION) ;
        if (!vases || !vases.categorizedBonuses || !vases.categorizedBonuses["RetentionBonuses"]) {
            return [];
        }
        console.log("RetentionBonuses getRetentionBonuses returns ",vases.categorizedBonuses["RetentionBonuses"].services);
        return vases.categorizedBonuses["RetentionBonuses"].services;
    }

    isVasInBonuses(vas) {
        let retentionBonuses = this.getRetentionBonuses();
        if (!retentionBonuses) {
            return false
        }

        let inBonuses = [];
        retentionBonuses.forEach(bonus => {
            if (bonus.id == vas.productCode)
                inBonuses.push(vas)
        })
        return !!inBonuses.length;
    }

    getSelectedBonus() {
        let selected = null;
        console.log("RetentionBonuses getSelectedBonus");
        console.log("RetentionBonuses",this.props.entry.vases);
        console.log("RetentionBonuses",this.props);

        this.props.entry.vases.forEach(cartVas => this.isVasInBonuses(cartVas) ? selected = cartVas : "");
        return selected;
    }
    isRetentionBonusSelected() {
        console.log("isRetentionBonusSelected");
        let ret = true;
        let entry=this.props.entry;
        if (!!this.props.vases && !!this.props.vases.find(mv => mv.process =="RETENTION")) {

            let retentionBonuses = this.getRetentionBonuses();
            if (retentionBonuses.length > 0) {
                if (this.props.entry.vases.length == 0) {
                    console.log("NO RETENTION BONUS SELECTED", entry);
                    return false;
                }
                ret=false;
                retentionBonuses.forEach(rb => {
                    if (entry.vases.find(vas => {
                        return vas.productCode == rb.id
                    })) {
                        ret = true;
                    }
                });
            }
        }
        console.log("isRetentionBonusSelected",ret);
        return ret;

    }

    render() {
        const header = this.props.detailsHeader ? this.props.detailsHeader : "Szczegóły";

        let vas = this.getSelectedBonus();

        return this.containsVases() ? (<div>
            <ProductSectionWrapper key={"productSectionWrapper_" + this.props.index}
                                   entry={this.props.entry}
                                   sectionTitle={this.props.sectionTitle}
                                   className="u-box-shadow--s">
                {vas ? <VasEntryComponent
                        key={"VAS" + vas.productCode + "_bonus"}
                        detailsHeader={this.props.detailsHeader}
                        showDetails={false}
                        entry={vas}
                        cartBundle={this.props.cartBundle}
                        bundle={this.props.bundle}
                        propositionId={this.props.propositionId}
                        topBorder={true}
                        onChangeClicked={this.addButtonClicked}
                    />

                    : <BuyMoreProductsComponent addButtonClicked={this.addButtonClicked} icon={this.props.icon}
                                                buyMoreLabel={this.props.buyMoreLabel}
                                                className={"g-brand2-c"}/>
                }
                <VasModal
                    title={"Wybierz bonus za przedłużenie umowy"}
                    cartVases={this.props.entry.vases}
                    vases={this.props.vases}
                    viewAsRetentionBonuses={true}
                    multiChoice={false}
                    header={header}
                    cartBundle={"" + this.props.entry.bundleNo}
                    bundle={this.props.entry.productCode}
                    propositionId={this.props.entry.bundleCode}
                    open={this.state.modalVisible}
                    onClose={this.closeModalClicked}
                    idx={this.props.idx}/>
            </ProductSectionWrapper>
            <MulticartValidationComponent messageType={"retentionBonuses"} iconType={"warn"}
                                          className={"u-padding-top-m"} scrollToParent={true}
                                          show={!this.isRetentionBonusSelected()}/>
        </div>) : null;
    }
}

const ProductSectionWrapper = (props) => {
    let icon = props.icon ? props.icon.split('_').join('-') : null;
    return (
        <div className={"g-white1-bg u-margin-top-l" + (props.className ? " " + props.className : "")}>
            {props.sectionTitle &&
            <h3 className="h3 u-small-hide u-padding-all-l u-no-spacing">{props.sectionTitle}</h3>}
            {props.children}
        </div>
    );
};
SelectRetentionBonusComponent.propTypes = {
    idx: PropTypes.number,
    entry: PropTypes.object,
    fetchMobileVases: PropTypes.func,
    vases: PropTypes.array
};
SelectRetentionBonusComponent.defaultProps = {
    buyMoreLabel: "Dziękujemy, że jesteś z nami. Wybierz bonus za przedłużenie umowy."
};

const mapStateToProps = (state) => ({
    vases: getMobileVases(state),
    validationMsg: getFrontValidationMsg(state)
});

const mapDispatchToProps = (dispatch) => ({
    fetchMobileVases: (propositionId, bundle, process) => dispatch(fetchMobileVases(propositionId, bundle, process))

});

export default connect(mapStateToProps, mapDispatchToProps)(SelectRetentionBonusComponent);

